@import "./vars.scss";
.tictac-board {
  width: 240px;
  height: 240px;
  display: flex;
  flex-wrap: wrap;
}
.tictac-cell {
  width: 80px;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid $titleColor;
  font-size: 60px;
  color: $mainColor;
}
.tic-tac-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}
.tic-tac__title {
  font-size: 32px;
  color: $titleColor;
  font-family: "Permanent Marker", cursive;
  margin-bottom: 10px;
}
.tic-tac__restart {
    border: none;
    padding: 0 5px;
    background-color: $titleColor;
    border-radius: 6px;
    transition: 0.4s;
    margin-bottom: 10px;
    font-size: 20px;
    &:hover {
      background-color: #5ce1ff;
    }
}
.tictac__winner {
    text-align: center;
    margin-bottom: 10px;
    color: $mainColor;
}